<template>
  <div v-if="showField()">
    <slot />
    <error-display :id="field.uuid" />
  </div>
</template>

<script>
import Validator from "../../field-conditions/Validator";
import ErrorDisplay from "./ErrorDisplay.vue";
import { validateForm } from "js-laravel-validation";
import { mapActions, mapGetters } from "vuex";
import { bus } from "../../eventBus";

export default {
  props: {
    field: Object,
    handle: String,
    defaults: Object,
  },
  computed: {
    ...mapGetters(["errorBag"]),
  },
  components: {
    ErrorDisplay,
  },
  methods: {
    ...mapActions(["setErrorBag"]),
    validateFieldset(sets) {
      let formData = {};
      sets.forEach((set, i) => {
        set.fields.forEach((field) => {
          if (!field.validate) return;

          console.log({ field });

          formData[`${this.field.uuid}-${field.handle}-${i}`] = {};

          formData[`${this.field.uuid}-${field.handle}-${i}`].value =
            Array.isArray(this.field.value[i][field.handle])
              ? this.field.value[i][field.handle].length || null
              : this.field.value[i][field.handle];
          formData[`${this.field.uuid}-${field.handle}-${i}`].validation =
            field.validate.join("|");
        });
      });
      const { errors } = validateForm({
        formData,
      });
      console.log({ errors, formData });
      return true;
    },
    validateField() {
      const is_set = this.field?.computed?.config?.sets;
      if (is_set) {
        is_set.forEach((set) => {
          if (!set.fields.some((field) => field.hasOwnProperty("validate"))) {
            return true;
          }
        });
        return this.validateFieldset(is_set);
      }

      if (!this.field?.computed?.config?.validate) return true;

      console.log(this.field);

      const errorIndex = this.errorBag.findIndex((error) =>
        error.hasOwnProperty(this.field.uuid)
      );

      const errorExists = errorIndex !== -1;

      const formData = {
        [this.field.uuid]: {
          value: this.field.value,
          validation: this.field?.computed?.config?.validate.join("|"),
        },
      };

      const { errors } = validateForm({ formData });
      console.log({ errors, formData, value: this.field.value });

      if (errorExists && !errors) {
        this.setErrorBag(
          this.errorBag.filter(
            (error) => !error.hasOwnProperty(this.field.uuid)
          )
        );
      }

      if (Object.keys(errors).length && !errorExists) {
        this.setErrorBag([...this.errorBag, errors]);
      }
    },
    showField() {
      const validator = new Validator(
        this.defaults[this.handle].config,
        this.field.value,
        {},
        "base"
      );
      return validator.passesConditions();
    },
  },
  mounted() {
    bus.$on("updated", (e) => {
      this.validateField();
    });
  },
};
</script>

