<template>
  <div class="module-selector">
    <eva-icon
      class="flex cursor-pointer pulse"
      fill="currentColor"
      name="settings-outline"
      width="18"
      height="18"
      @click="toggleStack = true"
    ></eva-icon>
    <stack name="field-stack" v-if="toggleStack" :beforeClose="handleClose">
      <div class="h-full p-4 bg-white overflow-auto">
        <div class="flex">
          <h2 class="mb-2 text-xl">Settings for {{ admin_label }}</h2>
          <button class="btn-primary ml-auto" @click="handleSave">Save</button>
        </div>
        <component
          :is="`${componentType}-settings`"
          :field="component"
          :fieldDefaults="fieldDefaults[`${componentType}s`]"
        />
      </div>
    </stack>
  </div>
</template>

<script>
import { EvaIcon } from "vue-eva-icons";
import FieldSettings from "../fields/FieldSettings.vue";
import SetSettings from "../sets/SetSettings.vue";
import RowSettings from "../rows/RowSettings.vue";
import SectionSettings from "../sections/SectionSettings.vue";
import { mapGetters, mapActions } from "vuex";
import OptionsFields from "../../mixins/OptionsFields";
import { validateForm } from "js-laravel-validation";

export default {
  props: {
    component: Object,
    value: Array,
    index: Number,
  },
  mixins: [OptionsFields],
  data() {
    return {
      toggleStack: false,
    };
  },
  computed: {
    ...mapGetters(["fieldDefaults", "dirtyFields", "errorBag"]),
    admin_label() {
      return (
        this.component.config?.buildamic_settings?.admin_label ||
        this.component.config.admin_label
      );
    },
    componentType() {
      return this.component.useSettings || this.component.type;
    },
  },
  methods: {
    ...mapActions(["setDirtyFields", "setErrorBag"]),
    handleSave() {
      //   this.validateFields();

      if (!this.errorBag.length) {
        this.toggleStack = false;
      }
    },
    revertFieldValues() {
      this.dirtyFields.forEach((field) => {
        this.setDeep(field.obj, field.fullPath, field.val);
      });
      this.setDirtyFields([]);
    },
    handleClose() {
      const conf = window.confirm(
        "Are you sure you want to close this modules settings without saving?"
      );
      if (conf) {
        this.revertFieldValues();
        this.toggleStack = false;
        return true;
      }

      return false;
    },
    validateFields() {
      if (!this.dirtyFields.length) this.toggleStack = false;

      this.setErrorBag([]);

      // This is to run a js-laravel-validation check on the *statamic* fields
      // Not for fields in design tab / options tab
      this.dirtyFields.forEach((dirtyField) => {
        if (!dirtyField.obj.computed?.config) return;

        let formData = {};

        if (dirtyField.obj.computed?.config?.sets) {
          dirtyField.obj.computed.config.sets.forEach((set, i) => {
            formData = {};
            set.fields.forEach((field) => {
              formData[field.handle] = {
                value: dirtyField.val[i][field.handle],
              };
              if (field?.validate?.length ?? false) {
                formData[field.handle].validation = field.validate.join();
              }
            });
          });
        }

        formData[dirtyField.obj.computed.config.handle] = {
          value: dirtyField.val,
        };
        if (dirtyField.obj.computed.config?.validate ?? false) {
          formData[dirtyField.obj.computed.config.handle].validation =
            dirtyField.obj.computed.config?.validate.join();
        }

        const { errors } = validateForm({ formData });

        if (errors && Object.keys(errors).length) {
          this.setErrorBag([...this.errorBag, ...Object.values(errors)]);
        }
      });
    },
  },
  components: {
    EvaIcon,
    FieldSettings,
    SetSettings,
    RowSettings,
    SectionSettings,
  },
};
</script>

